import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import pic11 from '../assets/images/pic11.jpg'

const Performance = (props) => (
    <div>
        <Helmet>
            <title>Performance</title>
            <meta name="description" content="Web Performance Page" />
        </Helmet>

        <div id="main" className="alt">
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h1>Performance</h1>
                    </header>
                    <span className="image main"><img src={pic11} alt="" /></span>
                    <p>We want users to interact meaningfully with what we build for the web. If it's a blog, we want people to read posts. If it's an online store, we want to turn prospective shoppers into buyers. If it's a social networking web app, we want visitors to write posts, upload photos, and interact with one other. Performance plays a significant role in the success of any online venture, as high performing sites engage and retain users better than poorly performing ones. Here are some case studies of how performance has affected engagement and conversions for several websites.</p>
                    <p>If high performance is an asset, then poor performance is a liability. Here are a couple case studies where poor performance had a negative impact on business goals</p>
                    <p>Because business is highly competitive, we're always cognizant of how our competitors are doing. In the same DoubleClick by Google study cited above, it was found that sites loading within 5 seconds had 70% longer sessions, 35% lower bounce rates, and 25% higher ad viewability than sites taking nearly four times longer at 19 seconds. To get a rough idea of how your site's performance compares with the competition, check out the Speed Scorecard tool.</p>
                    <ul className="pagination">
                        <li><span className="button small disabled">Prev</span></li>
                        <li><Link to="performance" className= "page active">1</Link></li>
                        <li><Link to="multi_device" className="page">2</Link></li>
                        <li><Link to="loading_speeds" className="page">3</Link></li>
                        <li><Link to="multi_device" className="page">4</Link></li>
                        <li><Link to="multi_device" className="page">5</Link></li>
                        <li><Link to="multi_device" className="page">6</Link></li>
                        <li><Link to="multi_device" className="button small">Next</Link></li>
                    </ul>
                </div>
            </section>
        </div>

    </div>
)

export default Performance


